<template>
  <div v-if='!auth' class="hello">
      <h1>Ваш личный кабинет для тестирования</h1>
    <h3>Добро пожаловать в ваш кабинет, здесь вы можете протестировать наше приложение
    </h3>
    <button :disabled="!statusU" onclick="document.getElementById('up').click()">
     {{buttonUpload}}
    </button>
    <input style="display:none" id="up" accept=".ogg, .opus" type="file" @change="submitFile()">
    <h3>Отправить текст для тестирования конспекта и тематики текста
    </h3>
    <textarea disabled v-model="textEx" name="" id="" cols="30" rows="10"></textarea>
    <h3>Тематика текста:{{typeText}}</h3>
    <h3>Конспект текста:</h3>
    <textarea disabled v-model="sumText" name="" id="" cols="30" rows="10"></textarea>
    <h3>Хотите выйти?</h3>
    <button @click="logout()">
     Выйти
    </button>
  </div>
  <div v-else>
    <router-link to="/">
    <button>
     Войти в систему
    </button>
    </router-link>
  </div>
</template>

<script>
import router from '../router/index.js';
import api from "../assets/js/api.js";
export default {
  name: "home",
  data(){
    return{
      statusU:true,
      buttonUpload:'Загрузить аудио файл формата .ogg, .opus',
      textEx:'',
      typeText:'',
      sumText:'',
      ex:true
    }
  },
  watch: {
    auth: function(val) {
    if(!val&&this.ex){
    router.push(`/`);
    this.$toast.error(`К сожалению, вы еще не вошли в аккаунт.`);
    }
    }
    },
  computed:{
    auth(){
      return this.$store.getters.get_auth;
    },
    name(){
      return this.$store.getters.get_name;
    },
    code(){
      return this.$store.getters.get_code;
    }
  },
  methods: {
   async submitFile(){
     this.statusU=false;
     let file=document.getElementById('up').files[0];
      this.buttonUpload='Распознавание текста'
      let msg = await api.textGet(file);
      let setId=setInterval(async() => {
        let msg1 = await api.status(msg);
        if(msg1.status==1){
          this.buttonUpload='Подготовка конспекта о тексте'
          this.textEx=msg1.text;
        }
        if(msg1.status==2){
            this.textEx=msg1.text;
            this.typeText=msg1.classi;
        }
        if(msg1.status==3){
          this.buttonUpload='Готово'
          setTimeout(() => {
            this.textEx=msg1.text;
            this.typeText=msg1.classi;
            this.sumText=msg1.summary;
            this.buttonUpload='Загрузить аудио файл формата .ogg, .opus';
            this.statusU=true;
          }, 500);
          clearInterval(setId);
        }
        if(msg1.status==4){
            this.textEx=msg1.message;
            this.buttonUpload='Загрузить аудио файл формата .ogg';
            this.statusU=true;
          clearInterval(setId);
        }
      }, 5000);

      
     },
    async sendText(){
    //  this.buttonUpload='Загрузка...'
    //  let msg = await api.textGet({ text });
    //  if(msg){
    //    this.typeText=msg.desc;
    //    this.sumText=msg.consp;
    //  }
      //typeText=
      //sumText=
    },
    logout() {
      this.ex=false;
      this.$store.commit('change_auth',false);
      api.logout();
      this.$toast.success(`Поздравляем,вы успешно покинули аккаунт`);
      router.push('/');
    },
  },
};
</script>

